// ==========================================================================
//   BASE HTML
// ==========================================================================
* {-o-transition-property: none!important; -moz-transition-property: none!important; -ms-transition-property: none!important; -webkit-transition-property: none!important; transition-property: none!important; -o-transform: none!important; -moz-transform: none!important; -ms-transform: none!important; -webkit-transform: none!important; transform: none!important; -webkit-animation: none!important; -moz-animation: none!important; -o-animation: none!important; -ms-animation: none!important; animation: none!important;}
body {width:100%!important; margin:0!important; padding:0!important; line-height:1.4; word-spacing:1.1pt; letter-spacing:0.2pt; font-family:Arial, Arial, Helvetica, sans-serif; color:#000; background:none; font-size:10pt;}
p {margin-top:0;}

h1,h2,h3,h4,h5,h6 {font-family:Helvetica, Arial, sans-serif;}
h1{font-size:17pt; line-height:18pt; margin:10pt 0 7pt 0;}
h2{font-size:15pt; line-height:16pt; margin:5pt 0 7pt 0;}
h3{font-size:13pt; line-height:14pt; margin:5pt 0 7pt 0;}
h4,h5,h6{font-size:11pt; line-height:12pt; margin:5pt 0;}

code {font:10pt Courier, monospace;} 
blockquote {margin:0.4em 0; padding:0.3em 0.5em; font-size:10pt; border:1px solid #000; width:95%; display:block;}
blockquote p {margin: 0; padding: 0.3em 0;}
hr {box-sizing:border-box; float:left; width:100%; height:1px; margin-bottom: 10pt; background-color:#ccc; font-size:1px; line-height:1px;}

a:link, a:visited {background:transparent; font-weight:700; text-decoration:underline; color:#333;}
a[href^="tel"], a[href^="sms"] {text-decoration: none;}

table {margin:1px; text-align:left;}
th {border-bottom:1px solid #333; font-weight:bold;}
td {border-bottom:1px solid #333; }
th,td {padding:4px 10px 4px 0;}
tfoot {font-style:italic;}
caption {background:#fff; margin-bottom:2em; text-align:left;}
thead {display:table-header-group;}
img {border: 0;}

// ==========================================================================
//   FORM
// ==========================================================================
form {
    margin: 0 0 10pt;
}

.form__row {
    width: 100%;
    padding: 0 0 5pt;
}

form label {
    display: block;
    padding: 0 0 2pt;
}

form input[type=text],
form input[type=password],
form select,
form textarea {
    width: 200pt;
}

form ul {
    overflow: hidden;
    margin: 0;
    padding: 0;
}

form ul li {
    float: left;
    width: 100%;
}

form .row p {
    margin: 0;
}

// ==========================================================================
//   IMG
// ==========================================================================
img {
    max-width: 100%;
    height: auto;
}

img.left {
    float: left;
    margin: 3pt 15pt 5pt 0;
}

img.right {
    float: right;
    margin: 3pt 0 5pt 15pt;
}

.aligncenter,
div.aligncenter {
    display: block;
    margin: 3pt auto 10pt auto;
}

.alignright {
    float:right;
    margin: 3pt 0 5pt 15pt;
}

.alignleft {
    float: left;
    margin: 3pt 15pt 5pt 0;
}

a img.alignright {
    float: right;
    margin: 3pt 0 5pt 15pt;
}

a img.alignnone {
    margin: 5pt 15pt 10pt 0;
}

a img.alignleft {
    float: left;
    margin: 3pt 15pt 10pt 0;
}

a img.aligncenter {
    display: block;
    margin-left: auto;
    margin-right: auto
}

// ==========================================================================
//   ADDITIONAL
// ==========================================================================
@mixin print-row {
    clear: both;
    padding: 0 0 10pt 0;

    &:before,
    &:after {
        content: '';
        display: table;
        clear: both;
    }
}

.slick-track,
.slick-list,
.slick-slide {
    position: relative!important;
    left: auto!important;
    top: auto!important;
    width: 100%!important;
    height: auto!important;
    padding: 0!important;
    opacity: 1!important;
    float: left;
}

.slick-cloned,
.slick-arrow,
.slick-dots {
    display: none!important;
}

.icon {
    display: inline-block;
    vertical-align: top;
}

.icon__svg {
    width: 100%;
    height: 100%;
}

.header {
    @include print-row;
    text-align: center;
}

.slider {
    padding: 10pt 0;
    text-align: center;
}

.slider__item {
    @include print-row;
}

.slider__fig {
    float: left;
    width: 59%;
}

.slider__fig--secondary {
    width: 36%;
    padding: 0 0 0 5%;

    &:first-child {
        padding: 0 5% 0 0;
    }
}

.plist {
    @include print-row;
}

.plist__item {
    float: left;
    width: 30%;
    padding: 0 0 10pt 0;

    &:nth-child(3n-1) {
        padding-left: 5%;
        padding-right: 5%;
    }

    &:nth-child(3n+1) {
        clear: left;
    }

    a {
       font-weight: 400;
        text-decoration: none; 
    }
}

.plist__img {
    display: block;
    text-align: center;
}

.plist__sub {
    display: block;
    font-style: italic;
}

.plist__name {
    display: block;
    font-size: 110%;
    font-weight: 700;
}

.plist__price {
    .icon {
        display: none;
    }
}

.plist__more {
    float: left;
    width: 100%;
    padding: 5pt 0;
    text-align: center;
}

.citation {
    padding: 35pt 0;
    background: transparent!important;
    text-align: center;

    .xl {
        font-size: 180%;
        font-weight: 700;
    }
}

.promo {
    font-style: italic;
    text-transform: uppercase;
}

.find-us {
    padding: 10pt 0;
}

.find-us__prologue {
    padding: 0 0 10pt 0;
}

.find-us__fig {
    @include print-row;
}

.find-us__img {
    float: left;
    width: 250pt;
    padding: 0 20pt 0 0;
}

.find-us__img--secondary {
    width: 242pt;
}

.bread {
    padding: 0 0 10pt 0;

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
    }

    li {
        display: inline-block;
        vertical-align: top;

        &:before {
            content: '>';
        }

        &:first-child {
            &:before {
                display: none;
            }
        }
    }

    a {
        font-weight: 400;
    }
}

.similar {
    @include print-row;
    padding: 15pt 0 10pt 0;
}

.post__intro {
    @include print-row;
}

.post__fig {
    float: right;
    width: 23%;
}

.post__row {
    @include print-row;   
}

.post__article {
    float: left;
    width: 70%;
}

.post__meta {
    font-size: 120%;
    font-weight: 700;

    .icon {
        display: none;
    }
}

.cta {
    @include print-row;
}

.store {
    @include print-row;   
}

.store__main {
    float: left;
    width: 60%;
}

.store__aside {
    float: right;
    width: 35%;
}

.store__xl {
    font-size: 110%;
    font-weight: 700;
}

.store__foot {
    float: left;
    width: 60%;
    padding: 10pt 0;

    a {
        display: inline-block;
        vertical-align: top;
        padding-right: 10pt;
    }
}

.social-btns {
    padding: 0 0 10pt 0;
}

.istream {
    @include print-row;
}

.istream__item {
    float: left;
    width: 20%;

    img {
        display: block;
    }
}

.person {
    @include print-row;
}

.person__article {
    float: left;
    width: 70%;
}

.person__fig {
    float: right;
    width: 25%;
}

.person__promo {
    font-style: italic;
    text-transform: uppercase;
}

.history__item {
    @include print-row;
}

.history__fig {
    float: left;
    width: 25%;
}

.history__txt {
    float: right;
    width: 70%;
}

.history__xl {
    font-size: 140%;
    font-weight: 700;
}

.not-found__meta {
    font-style: italic;
    text-transform: uppercase;
}

.hero {
    padding: 0 0 20pt 0;
    text-align: center;
}

.hero__meta {
    font-style: italic;
    text-transform: uppercase;
}

.who {
    @include print-row;
}

.who__prologue {
    float: left;
    width: 100%;
    padding: 0 0 15pt 0;
}

.who__item {
    float: left;
    width: 50%;
}

.who__fig {
    width: 150pt;
}

// ==========================================================================
//   HIDE
// ==========================================================================
.dialog, .hamburger, .header__main, .footer, .subnav, .history__nav,
.contact__aside, .not-found__logo, .hero__logo, .who__arrow
{display:none;}